body{
  background-color:rgb(59, 59, 59);
  background-image: url("/img/ryan-hutton-37733-unsplash.jpg");
  background-attachment: fixed;
  background-size: 120%;
}

.App {
  text-align: center;
  font-family: 'Carter One', cursive;
  color:rgb(238, 228, 228)
/* font-family: 'Dosis', sans-serif; */
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.bio{
  font-size:1.5em;
  font-family: 'Dosis', sans-serif;
  
  background-color: rgba(48, 46, 46, 0.431);
  padding:1em;
}
.center{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  padding: 25px 0 50px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}
.bottom-margin{
  margin-bottom:3em;
}
@media screen and (max-width: 600px) {
  body{
    background-image: url("./backgrounds/nick-ilyasov-782288-unsplash.jpg");
  }
}



